@import "variables";
ul {
    font-size: $font-sizeT;
}

a { text-decoration: none;
    img:hover {
    transform: scale(1.07);
}    
}
a img {
    width: 250px !important;

}
.fontR {
    font-family: 'Roboto Slab', serif;
    
    margin-top: 0% !important;
    color: $font-colorT;
}
.font {font-family: 'Fira Sans Extra Condensed', sans-serif;
    font-size: $font-size;
    color:$font-colorBody;
}
.bg-contact{
    margin: 0%;
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-image: url(../PNG/bg-contacto.jpg);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}
.contact-form{
   
    
    border-radius: 10px;
    padding: 72px 55px 90px 55px;
}
.contact-wrap{    
    width: 790px;
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    padding: 72px 55px 90px 55px;
    z-index: 1;
}
.bg-contact::before{
    content: "";
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.contact-header{
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 39px;
    color: black;
}